import { TextField } from "@mui/material";
import { useUserContext } from "../../context/context";
import { BASE_URL } from "../../environment";
import { useEffect, useState } from "react";
import {
    Button,
    Card,
    CardBody,
    FormGroup,
    Form,
    Container,
    Row,
    Col,
} from "reactstrap";

const Profile = () => {
    const token = sessionStorage.getItem("token");
    const userContext = useUserContext();
    const [firstName, setFirstName] = useState(userContext.userData.first_name);
    const [lastName, setLastName] = useState(userContext.userData.last_name);
    const [phone, setPhone] = useState(userContext.userData.phone);
    const [email, setEmail] = useState(userContext.userData.email);

    const saveChanges = () => {
        if (
            firstName === userContext.userData.first_name &&
            lastName === userContext.userData.last_name &&
            phone === userContext.userData.phone &&
            email === userContext.userData.email
        ) {
            userContext.setAlertMessage("No changes detected.");
            userContext.setAlertType("info");
            userContext.setOpenAlert(true);
            return;
        }

        const requestOptions = {
            method: "POST",
            headers: new Headers({
                "Content-Type": "application/json",
                Authorization: `Token ${token}`,
            }),
            body: JSON.stringify({
                first_name: firstName,
                last_name: lastName,
                phone: phone,
                email: email,
                mail_changed: !(email === userContext.userData.email),
                updated_by: userContext.userData.username,
            }),
        };
        fetch(BASE_URL + "/user/updateuserdetails", requestOptions)
            .then((response) => {
                if (response.status === 200) {
                    userContext.setUserData({
                        ...userContext.userData,
                        first_name: firstName,
                        last_name: lastName,
                        phone,
                        email,
                    });
                    userContext.setAlertMessage("Updated Successfully.");
                    userContext.setAlertType("success");
                    userContext.setOpenAlert(true);
                } else {
                    userContext.setAlertMessage("Error while updating.");
                    userContext.setAlertType("error");
                    userContext.setOpenAlert(true);
                }
            })
            .catch((error) => console.log(error));
    };

    const verifyEmail = () => {
        const requestOptions = {
            method: "GET",
            headers: new Headers({
                "Content-Type": "application/json",
                Authorization: `Token ${token}`,
            }),
        };
        fetch(BASE_URL + "/generateOTP", requestOptions)
            .then((response) => {
                if (response.status === 200) {
                    userContext.setAlertMessage(
                        "Verification Email Sent Successfully."
                    );
                    userContext.setAlertType("success");
                    userContext.setOpenAlert(true);
                } else if (response.status === 406) {
                    userContext.setAlertMessage("Please Enter a valid email.");
                    userContext.setAlertType("warning");
                    userContext.setOpenAlert(true);
                } else {
                    userContext.setAlertMessage("Error sending email.");
                    userContext.setAlertType("error");
                    userContext.setOpenAlert(true);
                }
            })
            .catch((error) => console.log(error));
    };

    useEffect(() => {
        if (Object.keys(userContext.userData).length === 0) {
            userContext.AuthenticateRoute();
        }
    }, []);

    useEffect(() => {
        setFirstName(userContext.userData.first_name);
        setLastName(userContext.userData.last_name);
        setPhone(userContext.userData.phone);
        setEmail(userContext.userData.email);
    }, [userContext.userData]);

    return (
        <>
            <div className="header pb-8 pt-5 pt-lg-8 d-flex align-items-center">
                <span className="mask bg-gradient-default opacity-8" />
            </div>
            <Container className="mt--7 mb-7" fluid>
                <Row>
                    <Col className="order-xl-1" xl="12">
                        <Card
                            className={`bg-${
                                userContext.theme === "dark"
                                    ? "dark"
                                    : "secondary"
                            } shadow`}
                        >
                            <CardBody>
                                <Form>
                                    <h6 className="heading-small text-muted mb-4">
                                        User information
                                    </h6>
                                    <div className="pl-lg-4">
                                        <Row>
                                            <Col lg="6" md="12">
                                                <FormGroup className="">
                                                    <label
                                                        className="form-control-label"
                                                        htmlFor="input-username"
                                                    >
                                                        Username
                                                    </label>
                                                    <TextField
                                                        className="form-control-alternative"
                                                        value={
                                                            userContext.userData
                                                                .username
                                                        }
                                                        id="input-username"
                                                        placeholder="Username"
                                                        type="text"
                                                        disabled
                                                        fullWidth
                                                        size="small"
                                                    ></TextField>
                                                </FormGroup>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col lg="6">
                                                <FormGroup>
                                                    <label
                                                        className="form-control-label"
                                                        htmlFor="input-first-name"
                                                    >
                                                        First name
                                                    </label>
                                                    <TextField
                                                        className="form-control-alternative"
                                                        value={firstName}
                                                        id="input-first-name"
                                                        placeholder="First name"
                                                        type="text"
                                                        onChange={(e) => {
                                                            setFirstName(
                                                                e.target.value
                                                            );
                                                        }}
                                                        fullWidth
                                                        size="small"
                                                    ></TextField>
                                                </FormGroup>
                                            </Col>
                                            <Col lg="6">
                                                <FormGroup>
                                                    <label
                                                        className="form-control-label"
                                                        htmlFor="input-last-name"
                                                    >
                                                        Last name
                                                    </label>
                                                    <TextField
                                                        className="form-control-alternative"
                                                        value={lastName}
                                                        id="input-last-name"
                                                        placeholder="Last name"
                                                        type="text"
                                                        onChange={(e) => {
                                                            setLastName(
                                                                e.target.value
                                                            );
                                                        }}
                                                        fullWidth
                                                        size="small"
                                                    ></TextField>
                                                </FormGroup>
                                            </Col>
                                        </Row>
                                    </div>
                                    <hr className="my-4" />
                                    {/* Address */}
                                    <h6 className="heading-small text-muted mb-4">
                                        Contact information
                                    </h6>
                                    <div className="pl-lg-4">
                                        <Row>
                                            <Col lg="6">
                                                <FormGroup>
                                                    <label
                                                        className="form-control-label"
                                                        htmlFor="input-phone"
                                                    >
                                                        Phone Number
                                                    </label>
                                                    <TextField
                                                        className="form-control-alternative"
                                                        id="input-phone"
                                                        value={phone}
                                                        placeholder="Phone Number"
                                                        type="number"
                                                        min="0"
                                                        onChange={(e) => {
                                                            setPhone(
                                                                e.target.value
                                                            );
                                                        }}
                                                        fullWidth
                                                        size="small"
                                                    ></TextField>
                                                </FormGroup>
                                            </Col>
                                            <Col lg="6">
                                                <FormGroup>
                                                    <label
                                                        className="form-control-label"
                                                        htmlFor="input-email"
                                                    >
                                                        Email address
                                                    </label>
                                                    <TextField
                                                        className="form-control-alternative"
                                                        id="input-email"
                                                        value={email}
                                                        placeholder="Email"
                                                        type="email"
                                                        onChange={(e) => {
                                                            setEmail(
                                                                e.target.value
                                                            );
                                                        }}
                                                        fullWidth
                                                        size="small"
                                                    ></TextField>
                                                    {userContext.userData
                                                        .email_validation ? (
                                                        <div
                                                            style={{
                                                                color: "green",
                                                            }}
                                                        >
                                                            <i className="ni ni-check-bold" />
                                                            &nbsp;
                                                            <small>
                                                                Email verified.
                                                            </small>
                                                        </div>
                                                    ) : (
                                                        <div
                                                            style={{
                                                                color: "red",
                                                            }}
                                                        >
                                                            <i className="ni ni-fat-remove" />
                                                            <small>
                                                                Email not
                                                                verified.&emsp;
                                                                <a
                                                                    href={"#"}
                                                                    onClick={
                                                                        verifyEmail
                                                                    }
                                                                >
                                                                    Verify Now.
                                                                </a>
                                                            </small>
                                                        </div>
                                                    )}
                                                </FormGroup>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col lg="6">
                                                <FormGroup>
                                                    <label
                                                        className="form-control-label"
                                                        htmlFor="input-city"
                                                    >
                                                        Organization Id
                                                    </label>
                                                    <TextField
                                                        className="form-control-alternative"
                                                        value={
                                                            userContext.userData
                                                                .organization_id
                                                        }
                                                        id="input-city"
                                                        placeholder="City"
                                                        type="text"
                                                        disabled
                                                        fullWidth
                                                        size="small"
                                                    ></TextField>
                                                </FormGroup>
                                            </Col>
                                            <Col lg="6">
                                                <FormGroup>
                                                    <label
                                                        className="form-control-label"
                                                        htmlFor="input-country"
                                                    >
                                                        Organization Name
                                                    </label>
                                                    <TextField
                                                        className="form-control-alternative"
                                                        value={
                                                            userContext.userData
                                                                .organization_name
                                                        }
                                                        id="input-country"
                                                        placeholder="Country"
                                                        type="text"
                                                        disabled
                                                        fullWidth
                                                        size="small"
                                                    ></TextField>
                                                </FormGroup>
                                            </Col>
                                        </Row>
                                    </div>
                                    <hr className="my-4" />
                                </Form>
                                <Row className="align-items-center">
                                    <Col xs="8">
                                        {/* <h3 className="mb-0">My Profile</h3> */}
                                    </Col>
                                    <Col className="text-right" xs="4">
                                        <Button
                                            color="primary"
                                            onClick={() => {
                                                saveChanges();
                                            }}
                                            size="sm"
                                        >
                                            Save Changes
                                        </Button>
                                    </Col>
                                </Row>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </>
    );
};

export default Profile;
