import { useState } from "react";
import { NavLink as NavLinkRRD, Link, useHistory } from "react-router-dom";
import { PropTypes } from "prop-types";

import {
    Collapse,
    DropdownMenu,
    DropdownItem,
    UncontrolledDropdown,
    DropdownToggle,
    Form,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Media,
    NavbarBrand,
    Navbar,
    NavItem,
    NavLink,
    Nav,
    Container,
    Row,
    Col,
} from "reactstrap";
import { BASE_URL } from "../../environment";
import { useUserContext } from "../../context/context";

const Sidebar = (props) => {
    const userContext = useUserContext();
    const history = useHistory();
    const [collapseOpen, setCollapseOpen] = useState();

    const toggleCollapse = () => {
        setCollapseOpen((data) => !data);
    };

    const closeCollapse = () => {
        setCollapseOpen(false);
    };

    const createLinks = (routes) => {
        return routes.map((prop, key) => {
            if (prop.name === "Login" || prop.name === "Register") {
                return null;
            }
            return (
                <NavItem key={key}>
                    <NavLink
                        to={prop.layout + prop.path}
                        tag={NavLinkRRD}
                        onClick={closeCollapse}
                    >
                        <i className={prop.icon} />
                        <span
                            style={{
                                color:
                                    userContext.theme === "dark"
                                        ? "white"
                                        : "black",
                            }}
                        >
                            {prop.name}
                        </span>
                    </NavLink>
                </NavItem>
            );
        });
    };

    const { routes } = props;

    return (
        <Navbar
            className={`navbar-vertical fixed-left navbar-${
                userContext.theme === "dark" ? "dark" : "light"
            } bg-${userContext.theme === "dark" ? "dark" : "white"}`}
            style={{ borderRight: "1px solid white" }}
            expand="md"
            id="sidenav-main"
        >
            <Container fluid>
                <button
                    className="navbar-toggler"
                    type="button"
                    onClick={toggleCollapse}
                >
                    <span className="navbar-toggler-icon" />
                </button>
                <NavbarBrand
                    className={`pt-0 ${
                        userContext.theme === "dark" ? "bg-dark" : ""
                    }`}
                >
                    <div>
                        <a href="/">
                            <img
                                alt="..."
                                src={
                                    userContext.theme === "dark"
                                        ? "https://files-campaignkart.s3.ap-south-1.amazonaws.com/logo/campaignkart_com-dark.png"
                                        : "https://files-campaignkart.s3.ap-south-1.amazonaws.com/logo/campaignkart_com.png"
                                }
                                width={100}
                                height={100}
                            />
                            <br />
                            <span className="sideBarLogoText">
                                CAMPAIGN KART
                            </span>
                        </a>
                    </div>
                </NavbarBrand>
                <Nav className="align-items-center d-md-none">
                    <UncontrolledDropdown nav>
                        <DropdownToggle nav>
                            <Media className="align-items-center">
                                <span className="avatar avatar-sm rounded-circle">
                                    <img
                                        alt="..."
                                        src={require("../../assets/img/theme/logo-800x800.jpg")}
                                    />
                                </span>
                            </Media>
                        </DropdownToggle>
                        <DropdownMenu
                            className={`dropdown-menu-arrow ${
                                userContext.theme === "dark" ? "bg-dark" : ""
                            }`}
                            right
                        >
                            <DropdownItem
                                className="noti-title"
                                header
                                tag="div"
                            >
                                <h6
                                    className={`text-${
                                        userContext.theme === "dark"
                                            ? "white"
                                            : "overflow"
                                    } m-0`}
                                >
                                    Welcome!
                                </h6>
                            </DropdownItem>
                            <DropdownItem
                                to="/admin/user-profile"
                                tag={Link}
                                className={
                                    userContext.theme === "dark"
                                        ? "bg-dark"
                                        : ""
                                }
                                style={{
                                    color:
                                        userContext.theme === "dark"
                                            ? "white"
                                            : "black",
                                }}
                            >
                                <i className="ni ni-single-02" />
                                <span>My profile</span>
                            </DropdownItem>
                            {(userContext.userData.is_superuser ||
                                userContext.userData.is_staff) && (
                                <DropdownItem
                                    className={
                                        userContext.theme === "dark"
                                            ? "bg-dark"
                                            : ""
                                    }
                                    style={{
                                        color:
                                            userContext.theme === "dark"
                                                ? "white"
                                                : "black",
                                    }}
                                >
                                    <i className="ni ni-settings-gear-65" />
                                    <span>
                                        <a
                                            href={BASE_URL + "/admin_panel/"}
                                            target="_blank"
                                            rel="noreferrer"
                                        >
                                            Admin Panel
                                        </a>
                                    </span>
                                </DropdownItem>
                            )}
                            <DropdownItem divider />
                            <DropdownItem
                                className={
                                    userContext.theme === "dark"
                                        ? "bg-dark"
                                        : ""
                                }
                                style={{
                                    color:
                                        userContext.theme === "dark"
                                            ? "white"
                                            : "black",
                                }}
                                onClick={(e) => {
                                    fetch(BASE_URL + "/logout")
                                        .then((response) => {
                                            sessionStorage.removeItem("token");
                                            sessionStorage.removeItem(
                                                "username"
                                            );
                                            userContext.setUserData({});
                                            history.push("/home");
                                        })
                                        .catch((error) => {
                                            alert("Error logging out.");
                                        });
                                }}
                            >
                                <i className="ni ni-user-run" />
                                <span>Logout</span>
                            </DropdownItem>
                        </DropdownMenu>
                    </UncontrolledDropdown>
                </Nav>
                <Collapse
                    navbar
                    isOpen={collapseOpen}
                    className={userContext.theme === "dark" ? "bg-dark" : ""}
                    style={{ border: collapseOpen ? "1px solid white" : "" }}
                >
                    <div
                        className="navbar-collapse-header d-md-none"
                        style={{ borderBottom: "1px solid white" }}
                    >
                        <Row>
                            <Col className="collapse-brand" xs="6"></Col>
                            <Col className="collapse-close" xs="6">
                                <button
                                    className="navbar-toggler"
                                    type="button"
                                    onClick={toggleCollapse}
                                >
                                    <span
                                        style={{
                                            backgroundColor:
                                                userContext.theme === "dark"
                                                    ? "white"
                                                    : "black",
                                        }}
                                    />
                                    <span
                                        style={{
                                            backgroundColor:
                                                userContext.theme === "dark"
                                                    ? "white"
                                                    : "black",
                                        }}
                                    />
                                </button>
                            </Col>
                        </Row>
                    </div>
                    <Nav navbar>{createLinks(routes)}</Nav>
                </Collapse>
            </Container>
        </Navbar>
    );
};

export default Sidebar;
