import { Autocomplete, Box, Button, TextField } from "@mui/material";
import { useUserContext } from "../../context/context";
import { useEffect, useState } from "react";
import { BASE_URL } from "../../environment";
import { Card, Container, Row } from "reactstrap";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TablePagination from "@mui/material/TablePagination";
import TableRow from "@mui/material/TableRow";
import { useHistory } from "react-router-dom";

const SubscriptionHistory = () => {
    const token = sessionStorage.getItem("token");
    const userContext = useUserContext();
    const history = useHistory();
    const columns = [
        { id: "subscription_id", label: "Subscription ID", minWidth: 170 },
        { id: "subscription_name", label: "Subscription Name", minWidth: 100 },
        {
            id: "start_date",
            label: "Start Date",
            minWidth: 170,
        },
        {
            id: "end_date",
            label: "End Date",
            minWidth: 170,
        },
    ];
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [rows, setRows] = useState([]);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };

    const fetchSubscription = () => {
        if (
            Object.keys(userContext.organizationList).length !== 0 &&
            userContext.organization
        ) {
            const org = userContext.organizationList.filter(
                (o) => o.value === userContext.organization
            )[0];
            const requestOptions = {
                method: "POST",
                headers: new Headers({
                    "Content-Type": "application/json",
                    Authorization: `Token ${token}`,
                }),
                body: JSON.stringify({
                    organization_id: org?.org_id,
                }),
            };
            fetch(
                BASE_URL + "/subscription/fetchsubscriptiondata",
                requestOptions
            )
                .then((response) => response.json())
                .then((data) => {
                    setRows(data.subscription_list);
                })
                .catch((error) => console.log(error));
        }
    };

    useEffect(() => {
        if (Object.keys(userContext.userData).length === 0) {
            userContext.AuthenticateRoute();
        }
    }, []);

    useEffect(() => {
        fetchSubscription();
    }, [userContext.organizationList]);

    return (
        <>
            <Container
                className="mb-8"
                fluid
                style={{
                    backgroundImage:
                        "url(" + require("../../assets/img/img4.jpg") + ")",
                    backgroundRepeat: "no-repeat",
                    backgroundSize: "cover",
                    minHeight: "60vh",
                }}
            >
                <Row>
                    <div
                        className="col"
                        style={{
                            margin: "100px 0 0 0",
                        }}
                    >
                        {(userContext.userData.is_superuser ||
                            userContext.userData.is_staff) && (
                            <Card
                                className={`${
                                    userContext.theme === "dark"
                                        ? "bg-dark"
                                        : "bg-white"
                                }`}
                                style={{
                                    maxWidth: "500px",
                                    padding: "0 20px",
                                }}
                            >
                                <Box sx={{ display: "flex" }}>
                                    <Autocomplete
                                        id="combo-box-demo"
                                        options={userContext.organizationList}
                                        sx={{ minWidth: 200, my: 2 }}
                                        renderInput={(params) => (
                                            <TextField
                                                {...params}
                                                label="Organization"
                                            />
                                        )}
                                        value={userContext.organization}
                                        onInputChange={(e, value) => {
                                            userContext.setOrganization(value);
                                        }}
                                        size="small"
                                        fullWidth
                                    />
                                    <Button
                                        onClick={() => fetchSubscription()}
                                        sx={{
                                            marginLeft: 2,
                                            height: 40,
                                            marginY: "auto",
                                        }}
                                        variant="outlined"
                                    >
                                        Apply
                                    </Button>
                                </Box>
                            </Card>
                        )}
                        <Card
                            className={`shadow my-6 ${
                                userContext.theme === "dark"
                                    ? "bg-dark px-4"
                                    : ""
                            }`}
                        >
                            <TableContainer sx={{ maxHeight: 440 }}>
                                <Table stickyHeader aria-label="sticky table">
                                    <TableHead>
                                        <TableRow>
                                            {columns.map((column) => (
                                                <TableCell
                                                    key={column.id}
                                                    align={column.align}
                                                    style={{
                                                        minWidth:
                                                            column.minWidth,
                                                    }}
                                                >
                                                    {column.label}
                                                </TableCell>
                                            ))}
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {rows
                                            .slice(
                                                page * rowsPerPage,
                                                page * rowsPerPage + rowsPerPage
                                            )
                                            .map((row) => {
                                                return (
                                                    <TableRow
                                                        hover
                                                        role="checkbox"
                                                        tabIndex={-1}
                                                        key={row.code}
                                                    >
                                                        {columns.map(
                                                            (column) => {
                                                                const value =
                                                                    row[
                                                                        column
                                                                            .id
                                                                    ];
                                                                return (
                                                                    <TableCell
                                                                        key={
                                                                            column.id
                                                                        }
                                                                        align={
                                                                            column.align
                                                                        }
                                                                    >
                                                                        {column.format &&
                                                                        typeof value ===
                                                                            "number"
                                                                            ? column.format(
                                                                                  value
                                                                              )
                                                                            : value}
                                                                    </TableCell>
                                                                );
                                                            }
                                                        )}
                                                    </TableRow>
                                                );
                                            })}
                                    </TableBody>
                                </Table>
                            </TableContainer>
                            <TablePagination
                                rowsPerPageOptions={[10, 25, 100]}
                                component="div"
                                count={rows.length}
                                rowsPerPage={rowsPerPage}
                                page={page}
                                onPageChange={handleChangePage}
                                onRowsPerPageChange={handleChangeRowsPerPage}
                            />
                        </Card>
                    </div>
                </Row>
            </Container>
        </>
    );
};

export default SubscriptionHistory;
