import { useUserContext } from "../../context/context";
import { BASE_URL } from "../../environment";
import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import {
    Button,
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Form,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Row,
    Col,
    Container,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
} from "reactstrap";
import TextField from "@mui/material/TextField";

const Login = () => {
    const history = useHistory();
    const userContext = useUserContext();
    const [username, setUsername] = useState();
    const [password, setPassword] = useState();
    const [validationUsername, setValidationUsername] = useState();
    const [error, setError] = useState(false);
    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);

    const resetPassword = () => {
        const requestOptions = {
            method: "POST",
            headers: new Headers({
                "Content-Type": "application/json",
            }),
            body: JSON.stringify({
                username: validationUsername,
            }),
        };

        fetch(BASE_URL + "/resetpasswordemail", requestOptions)
            .then((response) => {
                console.log(response.status);
                if (response.status === 200) {
                    return response.json();
                } else if (response.status === 409) {
                    throw response.statusText;
                } else {
                    throw response.statusText;
                }
            })
            .then((data) => {
                userContext.setAlertMessage(
                    "Email has been sent with the details to reset password."
                );
                userContext.setAlertType("success");
                userContext.setOpenAlert(true);
                setModal(!modal);
            })
            .catch((error) => {
                if (error === "Conflict") {
                    userContext.setAlertMessage(
                        "Email associated with this account is not valid. Please contact customer support."
                    );
                    userContext.setAlertType("warning");
                    userContext.setOpenAlert(true);
                } else {
                    userContext.setAlertMessage("Error in resetting Password.");
                    userContext.setAlertType("error");
                    userContext.setOpenAlert(true);
                }
            });
        toggle();
    };

    const login = () => {
        if (!username || !password) {
            return alert("Mandatory fields empty for logging In.");
        }

        const requestOptions = {
            method: "POST",
            headers: new Headers({
                "Content-Type": "application/json",
            }),
            body: JSON.stringify({
                username: username,
                password: password,
            }),
        };

        fetch(BASE_URL + "/login", requestOptions)
            .then((response) => {
                if (response.status === 200) {
                    return response.json();
                } else {
                    throw response.statusText;
                }
            })
            .then((data) => {
                sessionStorage.setItem("token", data.token);
                sessionStorage.setItem("username", data.username);
                history.push("/admin/index");
                setError(false);
            })
            .catch((error) => {
                setError(true);
                console.log(error);
            });
    };

    return (
        <Container>
            <Col
                className="authForm"
                style={{ width: "50%", margin: "0 auto" }}
            >
                <Modal isOpen={modal} toggle={toggle}>
                    <ModalHeader toggle={toggle}>Reset Password</ModalHeader>
                    <ModalBody>
                        Enter your username, so we can help you reset your
                        password.
                        <br />
                        <br />
                        <TextField
                            id="outlined-basic"
                            value={validationUsername}
                            label="Enter username"
                            variant="outlined"
                            onChange={(e) => {
                                console.log(e.target.value, validationUsername);
                                setValidationUsername(e.target.value);
                            }}
                            fullWidth
                        />
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={resetPassword}>
                            Send
                        </Button>
                    </ModalFooter>
                </Modal>
                <Card className="bg-secondary shadow border-0">
                    <CardHeader className="bg-transparent pb-5">
                        <div className="text-center mt-2">
                            <span>Sign in with credentials</span>
                        </div>
                    </CardHeader>
                    <CardBody className="px-lg-5 py-lg-5">
                        <Form role="form">
                            <FormGroup className="text-center">
                                <small style={{ color: "red" }}>
                                    {error
                                        ? "Invalid Credentials. Try again."
                                        : " "}
                                </small>
                            </FormGroup>
                            <FormGroup className="mb-3">
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="ni ni-circle-08" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="Username"
                                        type="text"
                                        autoComplete="new-email"
                                        onChange={(e) => {
                                            setUsername(e.target.value);
                                        }}
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="ni ni-lock-circle-open" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="Password"
                                        type="password"
                                        autoComplete="new-password"
                                        onChange={(e) => {
                                            setPassword(e.target.value);
                                        }}
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup className="custom-control custom-control-alternative custom-checkbox">
                                <input
                                    className="custom-control-input"
                                    id=" customCheckLogin"
                                    type="checkbox"
                                />
                                <label
                                    className="custom-control-label"
                                    htmlFor=" customCheckLogin"
                                >
                                    <span className="text-muted">
                                        Remember me
                                    </span>
                                </label>
                            </FormGroup>
                            <FormGroup className="text-center">
                                <Button
                                    className="my-4"
                                    color="primary"
                                    type="button"
                                    onClick={login}
                                >
                                    Sign in
                                </Button>
                            </FormGroup>
                        </Form>
                    </CardBody>
                </Card>
                <Row className="mt-3">
                    <Col xs="6">
                        <a
                            className="text-light"
                            href="#ResetPassword"
                            onClick={toggle}
                        >
                            <small>Forgot password?</small>
                        </a>
                    </Col>
                    <Col className="text-right" xs="6">
                        <a className="text-light" href="/auth/register">
                            <small>Create new account</small>
                        </a>
                    </Col>
                </Row>
            </Col>
        </Container>
    );
};

export default Login;
