import { BASE_URL } from "../environment";
import React, { createContext, useContext, useState } from "react";
import { useHistory } from "react-router-dom";
import { Alert } from "@mui/material";
import Snackbar from "@mui/material/Snackbar";

export const UserContext = createContext();

export function UserContextProvider(props) {
    const history = useHistory();
    const [theme, setTheme] = useState("light");
    const [userData, setUserData] = useState({});
    const [organization, setOrganization] = useState("");
    const [organizationList, setOrganizationList] = useState([]);
    const [openAlert, setOpenAlert] = useState(false);
    const [alertMessage, setAlertMessage] = useState("sdfsdf");
    const [alertType, setAlertType] = useState("success");

    const handleClose = (event, reason) => {
        if (reason === "clickaway") {
            return;
        }
        setOpenAlert(false);
    };

    const AuthenticateRoute = () => {
        const token = sessionStorage.getItem("token");

        if (token) {
            const requestOptions = {
                method: "GET",
                headers: new Headers({
                    "Content-Type": "application/json",
                    Authorization: `Token ${token}`,
                }),
            };
            fetch(BASE_URL + "/user/fetchuserdetails", requestOptions)
                .then((response) => {
                    if (response.status === 200) {
                        return response.json();
                    } else {
                        throw response.statusText;
                    }
                })
                .then((data) => {
                    if (
                        !data.subscription_active &&
                        new Date(data.subscription_start_date) <=
                            Date.now() <=
                            new Date(data.subscription_end_date)
                    ) {
                        setAlertMessage(
                            "Your subscription has ended. Please contact your organization."
                        );
                        setAlertType("error");
                        setOpenAlert(true);
                        if (!(data.is_staff || data.is_superuser)) {
                            data.organization_name = "Campaign Kart";
                            data.organization_id =
                                "b2df70bc-6f35-4cee-a8bc-e0710116593c";
                        }
                    } else {
                        if (!data.email_validation) {
                            setAlertMessage("Email verification Pending.");
                            setAlertType("warning");
                            setOpenAlert(true);
                        }
                    }
                    setOrganization(data.organization_name);
                    setUserData(data);

                    const requestOptions2 = {
                        method: "GET",
                        headers: new Headers({
                            "Content-Type": "application/json",
                            Authorization: `Token ${token}`,
                        }),
                    };
                    fetch(
                        BASE_URL + "/organization/fetchogranizationlist",
                        requestOptions2
                    )
                        .then((response) => response.json())
                        .then((data) => {
                            setOrganizationList(
                                data.organization_list &&
                                    data.organization_list.map((org) => {
                                        return {
                                            id: org[0],
                                            value: org[1],
                                            label: org[1],
                                            img: org[5],
                                            img_dark: org[6],
                                            org_id: org[0],
                                        };
                                    })
                            );
                        })
                        .catch((error) => console.log(error));
                })
                .catch((error) => {
                    console.log(error);
                    history.push("/auth/login");
                });
        } else {
            history.push("/auth/login");
        }
    };

    return (
        <UserContext.Provider
            value={{
                theme,
                setTheme,
                userData,
                setUserData,
                organization,
                setOrganization,
                organizationList,
                setOrganizationList,
                AuthenticateRoute,
                openAlert,
                setOpenAlert,
                alertMessage,
                setAlertMessage,
                alertType,
                setAlertType,
            }}
        >
            <Snackbar
                open={openAlert}
                autoHideDuration={5000}
                onClose={handleClose}
                anchorOrigin={{ vertical: "top", horizontal: "right" }}
            >
                <Alert
                    onClose={handleClose}
                    severity={alertType}
                    sx={{ width: "100%" }}
                >
                    {alertMessage}
                </Alert>
            </Snackbar>
            {props.children}
        </UserContext.Provider>
    );
}

export const useUserContext = () => useContext(UserContext);
