import { useUserContext } from "../../context/context";
import { BASE_URL } from "../../environment";
import { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import {
    DropdownMenu,
    DropdownItem,
    UncontrolledDropdown,
    DropdownToggle,
    Navbar,
    Nav,
    Container,
    Media,
} from "reactstrap";

const AdminNavbar = (props) => {
    const history = useHistory();
    const userContext = useUserContext();
    const [orgImage, setOrgImage] = useState(
        "../../assets/img/organizationLogo.jpg"
    );
    const [orgImageDark, setOrgImageDark] = useState(
        "../../assets/img/organizationLogo.jpg"
    );

    useEffect(() => {
        const orgData = userContext.organizationList
            ? userContext.organizationList.filter(
                  (option) => option.value === userContext.organization
              )
            : [];
        if (orgData.length) {
            setOrgImage(orgData[0].img);
            setOrgImageDark(orgData[0].img_dark);
        }
    }, [
        userContext.organizationList,
        userContext.organization,
        userContext.theme,
    ]);

    return (
        <>
            <Navbar
                className="navbar-top navbar-dark"
                expand="md"
                id="navbar-main"
            >
                <Container fluid>
                    {props.brandText === "Marketing Dashboard" && (
                        <div
                            style={{ display: "flex" }}
                            className="organizationLogo"
                        >
                            <img
                                alt="..."
                                src={
                                    userContext.theme === "light"
                                        ? orgImage
                                        : orgImageDark
                                }
                                width={100}
                                height={100}
                                style={{ marginRight: "30px" }}
                            />
                            <img
                                alt="..."
                                src={require("../../assets/img/googleAdLogo.png")}
                                width={100}
                                height={100}
                            />
                        </div>
                    )}
                    <span
                        style={{
                            fontSize: 40,
                            color: "white",
                            fontWeight: "bold",
                            margin:
                                props.brandText === "Marketing Dashboard"
                                    ? "0 auto"
                                    : "0 20px",
                        }}
                    >
                        {props.brandText}
                    </span>

                    <Nav className="align-items-center d-none d-md-flex" navbar>
                        <UncontrolledDropdown nav>
                            <DropdownToggle className="pr-0" nav>
                                <Media className="align-items-center">
                                    <span className="avatar avatar-sm rounded-circle">
                                        <img
                                            alt="..."
                                            src={require("../../assets/img/theme/logo-800x800.jpg")}
                                        />
                                    </span>
                                    <Media className="ml-2 d-none d-lg-block">
                                        <span className="mb-0 text-sm font-weight-bold">
                                            {userContext.userData.username}
                                        </span>
                                    </Media>
                                </Media>
                            </DropdownToggle>
                            <DropdownMenu
                                className={`dropdown-menu-arrow ${
                                    userContext.theme === "dark"
                                        ? "bg-dark"
                                        : ""
                                }`}
                                right
                            >
                                <DropdownItem
                                    className="noti-title"
                                    header
                                    tag="div"
                                >
                                    <h6
                                        className={`text-${
                                            userContext.theme === "dark"
                                                ? "white"
                                                : "overflow"
                                        } m-0`}
                                    >
                                        Welcome!
                                    </h6>
                                </DropdownItem>
                                <DropdownItem
                                    to="/admin/user-profile"
                                    tag={Link}
                                    className={
                                        userContext.theme === "dark"
                                            ? "bg-dark"
                                            : ""
                                    }
                                    style={{
                                        color:
                                            userContext.theme === "dark"
                                                ? "white"
                                                : "black",
                                    }}
                                >
                                    <i className="ni ni-single-02" />
                                    <span>My profile</span>
                                </DropdownItem>
                                {(userContext.userData.is_superuser ||
                                    userContext.userData.is_staff) && (
                                    <DropdownItem
                                        className={
                                            userContext.theme === "dark"
                                                ? "bg-dark"
                                                : ""
                                        }
                                        style={{
                                            color:
                                                userContext.theme === "dark"
                                                    ? "white"
                                                    : "black",
                                        }}
                                    >
                                        <i className="ni ni-settings-gear-65" />
                                        <span>
                                            <a
                                                href={BASE_URL + "/admin_panel/"}
                                                target="_blank"
                                                rel="noreferrer"
                                            >
                                                Admin Panel
                                            </a>
                                        </span>
                                    </DropdownItem>
                                )}
                                <DropdownItem divider />
                                <DropdownItem
                                    className={
                                        userContext.theme === "dark"
                                            ? "bg-dark"
                                            : ""
                                    }
                                    style={{
                                        color:
                                            userContext.theme === "dark"
                                                ? "white"
                                                : "black",
                                    }}
                                    onClick={(e) => {
                                        fetch(BASE_URL + "/logout")
                                            .then((response) => {
                                                sessionStorage.removeItem(
                                                    "token"
                                                );
                                                sessionStorage.removeItem(
                                                    "username"
                                                );
                                                userContext.setUserData({});
                                                history.push("/home");
                                            })
                                            .catch((error) => {
                                                alert("Error logging out.");
                                            });
                                    }}
                                >
                                    <i className="ni ni-user-run" />
                                    <span>Logout</span>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                    </Nav>
                </Container>
            </Navbar>
        </>
    );
};

export default AdminNavbar;
