import { useUserContext } from "../../context/context";
import { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import {
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    UncontrolledCollapse,
    Navbar,
    NavItem,
    NavLink,
    Nav,
    Container,
    Row,
    Col,
} from "reactstrap";

const AdminNavbar = () => {
    const history = useHistory();
    const userContext = useUserContext();
    const token = sessionStorage.getItem("token");
    const [dropdownOpen, setDropdownOpen] = useState(false);
    const toggle = () => setDropdownOpen((prevState) => !prevState);
    const scrollTo = (data) => {
        document.getElementsByClassName(data)[0] &&
            document
                .getElementsByClassName(data)[0]
                .scrollIntoView({ behavior: "smooth", block: "center" });
    };
    const currentPage =
        window.location.href.split("/")[
            window.location.href.split("/").length - 1
        ];

    return (
        <>
            <Navbar
                className="navbar-top navbar-horizontal navbar-light authNavbar"
                expand="md"
            >
                <Container className="px-4">
                    <img
                        alt="..."
                        src={require("../../assets/img/logo.png")}
                        width={120}
                        height={120}
                    />
                    <span
                        className="authNavbarLogoText"
                        style={{
                            fontSize: 30,
                            color: "black",
                            fontWeight: "bold",
                        }}
                    >
                        CAMPAIGN KART
                    </span>
                    <button
                        className="navbar-toggler"
                        id="navbar-collapse-main"
                    >
                        <span className="navbar-toggler-icon" />
                    </button>
                    <UncontrolledCollapse
                        navbar
                        toggler="#navbar-collapse-main"
                    >
                        <div className="navbar-collapse-header d-md-none">
                            <Row>
                                <Col className="collapse-brand" xs="6">
                                    <Link to="/">
                                        <img
                                            alt="..."
                                            src={require("../../assets/img/logo.png")}
                                        />
                                    </Link>
                                </Col>
                                <Col className="collapse-close" xs="6">
                                    <button
                                        className="navbar-toggler"
                                        id="navbar-collapse-main"
                                    >
                                        <span />
                                        <span />
                                    </button>
                                </Col>
                            </Row>
                        </div>
                        <Nav className="ml-auto" navbar>
                            {token && (
                                <NavItem>
                                    <NavLink
                                        className="nav-link-icon"
                                        to="/admin/index"
                                        tag={Link}
                                    >
                                        <i className="ni ni-tablet-button" />
                                        <span className="nav-link-inner--text">
                                            Dashboard
                                        </span>
                                    </NavLink>
                                </NavItem>
                            )}
                            {currentPage === "home" ? (
                                <NavItem>
                                    <NavLink className="nav-link-icon">
                                        <Dropdown
                                            isOpen={dropdownOpen}
                                            toggle={toggle}
                                        >
                                            <span>
                                                <DropdownToggle
                                                    caret
                                                    style={{ border: "none" }}
                                                    className="nav-link-icon"
                                                >
                                                    <i className="ni ni-books" />
                                                    Services
                                                </DropdownToggle>
                                            </span>
                                            <DropdownMenu>
                                                <DropdownItem
                                                    onClick={() =>
                                                        scrollTo("fourthPart")
                                                    }
                                                >
                                                    Paid Advertising
                                                </DropdownItem>
                                                <DropdownItem
                                                    onClick={() =>
                                                        scrollTo("fifthPart")
                                                    }
                                                >
                                                    Integrated Marketing
                                                    Dashboard
                                                </DropdownItem>
                                            </DropdownMenu>
                                        </Dropdown>
                                    </NavLink>
                                </NavItem>
                            ) : (
                                <NavItem>
                                    <NavLink
                                        className="nav-link-icon"
                                        to="/home"
                                        tag={Link}
                                    >
                                        <i className="ni ni-building" />
                                        <span className="nav-link-inner--text">
                                            Home
                                        </span>
                                    </NavLink>
                                </NavItem>
                            )}
                            <NavItem>
                                <NavLink
                                    className="nav-link-icon"
                                    to="/aboutUs"
                                    tag={Link}
                                >
                                    <i className="ni ni-single-02" />
                                    <span className="nav-link-inner--text">
                                        About Us
                                    </span>
                                </NavLink>
                            </NavItem>
                            {Object.keys(userContext.userData).length === 0 &&
                                !token && (
                                    <>
                                        <NavItem>
                                            <NavLink
                                                className="nav-link-icon"
                                                to="/auth/login"
                                                tag={Link}
                                            >
                                                <i className="ni ni-key-25" />
                                                <span className="nav-link-inner--text">
                                                    Login
                                                </span>
                                            </NavLink>
                                        </NavItem>
                                        <NavItem>
                                            <NavLink
                                                className="nav-link-icon"
                                                to="/auth/register"
                                                tag={Link}
                                            >
                                                <i className="ni ni-circle-08" />
                                                <span className="nav-link-inner--text">
                                                    Register
                                                </span>
                                            </NavLink>
                                        </NavItem>
                                    </>
                                )}
                        </Nav>
                    </UncontrolledCollapse>
                </Container>
            </Navbar>
        </>
    );
};

export default AdminNavbar;
