import { useUserContext } from "../../context/context";
import { BASE_URL } from "../../environment";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import {
    Button,
    Card,
    CardHeader,
    CardBody,
    FormGroup,
    Form,
    Input,
    InputGroupAddon,
    InputGroupText,
    InputGroup,
    Row,
    Col,
    Container,
} from "reactstrap";

const Register = () => {
    const history = useHistory();
    const userContext = useUserContext();
    const [username, setUsername] = useState();
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();

    const register = () => {
        if (!username || !password || !email) {
            return alert("Mandatory fields empty to register.");
        }

        const requestOptions = {
            method: "POST",
            headers: new Headers({
                "Content-Type": "application/json",
            }),
            body: JSON.stringify({
                username: username,
                email: email,
                password: password,
            }),
        };

        fetch(BASE_URL + "/register", requestOptions)
            .then((response) => {
                console.log(response);
                if (response.status === 200) {
                    return response.json();
                } else if (response.status === 406) {
                    throw "Username already exists.";
                } else if (response.status === 403) {
                    throw "Enter a valid email.";
                } else {
                    throw "Registration Failed";
                }
            })
            .then((data) => {
                userContext.setAlertMessage(
                    "Registered Successfully. Login to continue."
                );
                userContext.setAlertType("success");
                userContext.setOpenAlert(true);
                history.push("/auth/login");
            })
            .catch((error) => {
                userContext.setAlertMessage(error);
                userContext.setAlertType("error");
                userContext.setOpenAlert(true);
                console.log(error);
            });
    };

    return (
        <Container>
            <Col
                className="authForm"
                style={{ width: "50%", margin: "0 auto" }}
            >
                <Card className="bg-secondary shadow border-0">
                    <CardHeader className="bg-transparent pb-5">
                        <div className="text-center mt-2">
                            <span>Sign up with credentials</span>
                        </div>
                    </CardHeader>
                    <CardBody className="px-lg-5 py-lg-5">
                        <Form role="form">
                            <FormGroup>
                                <InputGroup className="input-group-alternative mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="ni ni-circle-08" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="Username"
                                        type="text"
                                        onChange={(e) => {
                                            setUsername(e.target.value);
                                        }}
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <InputGroup className="input-group-alternative mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="ni ni-email-83" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="Email"
                                        type="email"
                                        autoComplete="new-email"
                                        onChange={(e) => {
                                            setEmail(e.target.value);
                                        }}
                                    />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <InputGroup className="input-group-alternative">
                                    <InputGroupAddon addonType="prepend">
                                        <InputGroupText>
                                            <i className="ni ni-lock-circle-open" />
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        placeholder="Password"
                                        type="password"
                                        autoComplete="new-password"
                                        onChange={(e) => {
                                            setPassword(e.target.value);
                                        }}
                                    />
                                </InputGroup>
                            </FormGroup>
                            <Row className="my-4">
                                <Col xs="12">
                                    <div className="custom-control custom-control-alternative custom-checkbox">
                                        <input
                                            className="custom-control-input"
                                            id="customCheckRegister"
                                            type="checkbox"
                                        />
                                        <label
                                            className="custom-control-label"
                                            htmlFor="customCheckRegister"
                                        >
                                            <span className="text-muted">
                                                I agree with the{" "}
                                                <a
                                                    href="#pablo"
                                                    onClick={(e) =>
                                                        e.preventDefault()
                                                    }
                                                >
                                                    Privacy Policy
                                                </a>
                                            </span>
                                        </label>
                                    </div>
                                </Col>
                            </Row>
                            <div className="text-center">
                                <Button
                                    className="mt-4"
                                    color="primary"
                                    type="button"
                                    onClick={register}
                                >
                                    Create account
                                </Button>
                            </div>
                        </Form>
                    </CardBody>
                </Card>
            </Col>
        </Container>
    );
};

export default Register;
